.footer {
  display: flex;
  align-items: center;
  margin: -0.5rem -3.5rem;
  position: relative;
}
.f-content {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  margin-top: 4rem;
  gap: 4rem;
  color: white;
}
.f-content > :nth-child(1){
  display: flex;
  margin-top: 2rem;


} 
.f-icons {
  display: flex;
  gap: 2rem;

}

@media screen and (max-width: 480px){
  .f-content{
    transform: scale(.5);
    top: 1rem;
  }
  .f-icons {
    transform: scale(.5);
    top: 1rem;
  }
}
